<template>
  <section class="content page-privacy-policy">
    <div class="col-md-12 col-lg-12">
      <div class="box box-primary">
        <div class="box-header with-border" @mouseenter="showEdit" @mouseleave="showEdit" @click="edited">
          <h3 class="box-title">Privacy Polic Template</h3>
          <i class="fa fa-pencil" v-if="showEditIcon"></i>
        </div>
        <div class="box-body">
          <div class="html ql-editor" v-html="content" v-if="!editMod"></div>
          <div class="html ql-editor" v-html="content" v-if="editMod"></div>
          <div class="quill-editor-example" v-if="editMod">
            <quill-editor
              ref="myTextEditor"
              v-model="content"
              :options="editorOption"
            >
            </quill-editor>
          </div>
          <div v-if="editMod">
            <button
              :class="btn.class"
              :key="index"
              @click="btn.onClick"
              v-for="(btn, index) in buttonOptions"
            >
              {{btn.text}}
            </button>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import main from './privacyPolicy.js'
export default main
</script>

<style lang="sass">
@import './privacyPolicy.sass'
</style>



