export default {
  name: 'App',
  data () {
    return {
      section: 'Head'
    }
  },
  mounted () {
    if (this.$route.path !== '/login') {
      this.$api.checkToken().catch(error => {
        console.log(error)
        this.logout()
      })
    }
  },
  methods: {
    logout () {
      this.$store.commit('SET_TOKEN', null)
      this.$store.commit('SET_USER', '')
      this.$router.app.$cookie.delete('sid')
      window.localStorage.clear()

      this.$router.push('/login')
    }
  }
}



