import _ from 'lodash'

import form from '../../form'

export default {
  name: 'Theme',
  props: {
    saveTo: { type: String },
    text: { type: Object, required: true },
    title: { type: String },
    file: { type: Object }
  },
  data () {
    return {
      alert$: {
        show: false
      },
      buttons: [
        {
          class: 'btn btn-primary pull-right',
          onClick: () => { },
          text: 'Save'
        }
      ],
      loading: false
    }
  },
  methods: {
    alert (style, message, timeout = 2000) {
      this.alert$.class = 'alert-' + style
      this.alert$.text = message
      this.alert$.show = true

      clearTimeout(this.alert._timeout)
      if (timeout) {
        this.alert._timeout = setTimeout(
          () => (this.alert$.show = false),
          timeout
        )
      }
    },
    fieldType (type) {
      return type || 'text'
    },
    getImageSrc (id) {
      let ts = Date.now()
      return `/theme/${id}?${ts}`
    },
    failedCallback (e) {
      this.loading = false
      this.alert('danger', `Something went wrong, please try again (Error: ${e})`)
      for (const key in this.file) {
        this.file[key].isLocal = false
      }
    },
    successCallback () {
      this.loading = false
      this.alert('success', 'Success')
      for (const key in this.file) {
        this.file[key].isLocal = false
      }
    },
    async getTheme () {
      const config = await this.$api.getTheme()
        .then(r => r.data)

      Object.keys(this.text).forEach((blockKey) => {
        const obj = this.text[blockKey]
        for (let key in obj) {
          const input = obj[key]
          const value = config[key]
          if (value) input.model = value
        }
      })

      return config
    },
    isText (type) {
      return this.fieldType(type) === 'text'
    },
    isTextarea (type) {
      return this.fieldType(type) === 'textarea'
    },
    async updateTheme (e) {
      this.loading = true
      this.alert('info', 'Uploading...', 0)
      const formData = form.getFormData(e)

      if (this.saveTo) {
        const data = form.getJsonFormData(formData)
        await this.$api[this.saveTo](data)
          .then(
            () => this.successCallback(),
            (e) => this.failedCallback(e),
          )
      } else {
        await this.$api.updateTheme(formData)
          .then(() => this.successCallback())
      }
    },
    onFileChange (e) {
      const invalid = _.some(e.target.files, file => !file.type.match('image'))
      if (invalid) e.target.value = ''
    },
    removeImage (id) {
      this.file[id].isLocal = true
      document.getElementById(id).value = ''
    },
    submit (e) {
      this.updateTheme(e)
    },
    whenImageNotFound (id) {
      this.file[id].isLocal = true
    },
    showColorPicker () {
      if (window.jQuery('#primary_color')) {
        window.jQuery('#primary_color')
          .colorpicker({
            format: 'hex'
          })
          .on('changeColor', e => {
            if (e.value && this.text.primary_color) this.text.primary_color.model = e.value
          })
      }
    }
  },
  mounted () {
    // NOTE: This api call is just for confirm the color picker can shows correctly.
    return this.getTheme()
      .then(() => this.showColorPicker())
  }
}



