import Tab from '../../Tab/Tab'
import Tabs from '../../Tabs/Tabs'
import Theme from '../../Theme/Theme'

export default {
  components: {
    Tab,
    Tabs,
    Theme
  },
  data () {
    return {
      fieldsData: {
        appText: {
          wifi_name: {
            class: 'form-control',
            label: 'Wi-Fi Pattern Name',
            model: null,
            validate: {
              rules: {
                max: 32
              }
            }
          }
        },
        appFile: {
          'logo_navbar.png': {
            class: 'form-control-file',
            isLocal: false,
            label: 'Navbar Logo'
          }
        },
        settingsText: {
          product_name: {
            class: 'form-control',
            label: 'Product Name',
            model: null,
            validate: {
            }
          },
          primary_color: {
            class: 'form-control',
            label: 'Primary Color',
            model: null,
            validate: {
              rules: {
                regex: /^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/
              }
            }
          }
        },
        settingsFile: {
          'logo.png': {
            class: 'form-control-file',
            isLocal: false,
            label: 'App Logo'
          }
        },
        emailBasicText: {
          company_name: {
            class: 'form-control',
            label: 'Company Name',
            model: null,
            validate: {
            }
          },
          company_url: {
            class: 'form-control',
            label: 'Company Website Url',
            model: null,
            validate: {
            }
          },
          company_address: {
            class: 'form-control',
            label: 'Company Address',
            model: null,
            validate: {
            }
          },
          company_contact: {
            class: 'form-control',
            label: 'Company Contact',
            model: null,
            validate: {
            }
          },
          apple_store: {
            class: 'form-control',
            label: 'Apple Store Url',
            model: null,
            validate: {
            }
          },
          google_play: {
            class: 'form-control',
            label: 'Google Play Url',
            model: null,
            validate: {
            }
          },
          app_name: {
            class: 'form-control',
            label: 'App Name',
            model: null,
            validate: {
            }
          },
          app_url_scheme: {
            class: 'form-control',
            label: 'App Url Scheme',
            model: null,
            validate: {
            }
          }
        },
        emailTemplateText: {
          welcome_email_subject: {
            class: 'form-control',
            label: 'Welcome Email Subject',
            model: null,
            validate: {
            }
          },
          welcome_email_headline: {
            class: 'form-control',
            label: 'Welcome Email Headline',
            model: null,
            validate: {
            }
          },
          welcome_email_body: {
            class: 'form-control',
            label: 'Welcome Email Body',
            model: null,
            validate: {
            }
          },
          welcome_web_headline: {
            class: 'form-control',
            label: 'Welcome Web Headline',
            model: null,
            validate: {
            }
          },
          welcome_web_body: {
            class: 'form-control',
            label: 'Welcome Web Body',
            model: null,
            validate: {
            }
          }
        },
        legalPoliciesText: {
          title: {
            class: 'form-control',
            label: 'Web Headline',
            model: null,
            type: 'text',
            validate: {
            }
          },
          body: {
            class: 'form-control',
            label: 'Web Body',
            model: null,
            type: 'textarea',
            validate: {
            }
          }
        }
      },
      muranoTermsUrl: `https://exosite.com/murano-terms-conditions/`,
      legalUrl: `${window.location.origin}/static/legal.html`
    }
  },
  methods: {
    async getTheme () {
      // TODO: Make sure there has no conflict keys when do the Object.assign()
      const config = await Promise.all([
        this.$api.getTheme(),
        this.$api.getLegal()
      ]).then(([themeData, legalData]) => Object.assign({}, themeData.data, legalData.data))

      Object.keys(this.$data.fieldsData).forEach((blockKey) => {
        const obj = this.$data.fieldsData[blockKey]
        for (let key in obj) {
          const input = obj[key]
          const value = config[key]
          if (value) input.model = value
        }
      })

      return config
    },
    showColorPicker () {
      if (window.jQuery('#primary_color')) {
        window.jQuery('#primary_color')
          .colorpicker({
            format: 'hex'
          })
          .on('changeColor', e => {
            if (e.value && this.settingsText.primary_color) this.settingsText.primary_color.model = e.value
          })
      }
    }
  },
  async mounted () {
    return this.getTheme()
      .then(() => this.showColorPicker())
  }
}



