import bus from '../../../bus'
import timeLine from '../Timeline/Timeline.vue'

export default {
  components: {
    timeLine
  },
  computed: {
    getAlexaStatus () {
      if (this.alexa_status === false) return 'Not connected'
      if (this.alexa_status === true) return 'Connected'
      return '--'
    },
    collapseStatus () {
      return this.isToggle
    },
    getConnectivityStatus () {
      if (this.connectivityStatus === 0) return 'Offline'
      if (this.connectivityStatus === 1) return 'Online'
      return '--'
    },
    getConnectivityStatusIcon () {
      return this.connectivityStatus
        ? 'fa fa-circle device-online-icon'
        : 'fa fa-circle device-offline-icon'
    },
    getDeviceStatus () {
      return this.deviceInfo.device_status || []
    },
    getFirmwareVersion () {
      return this.firmwareVersion || '--'
    },
    guest () {
      return this.getUsers.guest || []
    },
    index () {
      return 0
    },
    isDeleted () {
      return this.deleted
    },
    getModel () {
      return this.model || '--'
    },
    owner () {
      return this.getUsers.owner || []
    },
    serialNumber () {
      return this.sn
    },
    getUsers () {
      return (
        this.deviceInfo.users || {
          guest: [],
          owner: []
        }
      )
    }
  },
  data () {
    return {
      deviceInfo: {},
      isCollapsed: false,
      isToggle: false
    }
  },
  methods: {
    async collapsed (sn) {
      window.jQuery(`#${sn}`).collapse('toggle')
      this.isToggle = !this.isToggle
      if (!this.isCollapsed) {
        this.deviceInfo = await this.$api.getDeviceInfo({sn})
        .then(ret => ret.data)
        .catch(e => console.log(e))
      }
      this.isCollapsed = true
    },
    deletePop (sn) {
      bus.$emit('dialog-show', {
        title: 'Delete Device',
        text: `
Are you sure you want to delete
<small class="label label-default">${sn}</small>?
        `,
        buttons: [
          {
            text: 'Close'
          },
          {
            text: 'Delete',
            click: () => {
              bus.$emit('dialog-hide')
              this.deleteDevice(sn)
            }
          }
        ]
      })
    },
    deleteDevice (sn) {
      this.$api.removeDevice(sn)
        .then(
          () => {
            bus.$emit('DeviceDeletePopup-removeDevice', sn)
          }
        )
    }
  },
  props: {
    connectivityStatus: {
      type: Number
    },
    firmwareVersion: {
      type: String
    },
    model: {
      type: String
    },
    deleted: {
      type: Boolean
    },
    sn: {
      type: String
    }
  }
}



