import bus from '../../../bus.js'

export default {
  name: 'DeletePopUp',
  beforeCreate () {
    bus.$on('getFwInfo', (data) => {
      this.info = data
    })
  },
  data () {
    return {
      info: {
        name: null,
        previousVersion: null,
        sha1: null,
        size: null,
        url: null,
        version: null
      }
    }
  },
  methods: {
    close () {
      window.jQuery('#delete-form').modal('hide')
    },
    async deleteFw () {
      const data = `id=${this.info.name}`
      try {
        await this.$api.removeFirmware(data)
        bus.$emit('getFirmwareList')
        window.jQuery('#delete-form').modal('hide')
      } catch (error) {
        console.log(error)
      }
    }
  }
}



