<template>
  <div id="accordion" class="panel box box-header-row">
    <div @click="collapsed(serialNumber)">
      <div :class="['box-header', { clicked: collapseStatus } ]">
        <div class="collapsed" data-toggle="collapse" data-parent="#accordion" aria-expanded="false" aria-controls="collapseTwo">
          <div class="col-sm-5 box-title">{{serialNumber}}</div>
          <div class="col-sm-2 box-title">{{getModel}}</div>
          <div class="col-sm-2 box-title">
            <i v-if="getConnectivityStatus !== '--'" :class="getConnectivityStatusIcon"></i> {{getConnectivityStatus}}</div>
          <div class="col-sm-2 box-title">{{getFirmwareVersion}}</div>
          <div class="col-sm-1 box-title">
            <a v-if="!isDeleted" @click.stop="deletePop(serialNumber)">
              <i class="fa fa-trash fa-2"></i>
            </a>
          </div>
        </div>
      </div>
    </div>
    <div :id="serialNumber" class="panel-collapse collapse" role="tabpanel" aria-labelledby="headingTwo">
      <div class="box-body">
        <time-line
          :connectivity-status="getConnectivityStatus"
          :device-status="getDeviceStatus"
          :is-clicked="isCollapsed"
          :target="serialNumber"
        ></time-line>
        <div class="col-sm-12">
          <div class="box box-gray">
            <div class="box-header with-border">
              <h3 class="box-title">Users</h3>
            </div>
            <div class="box-body">
              <dl class="dl-horizontal">
                <div class="col-sm-6">
                  <dt>OWNER</dt>
                  <dd v-for="(value, index) in owner" :key="index">{{value}}</dd>
                </div>
                <div class="col-sm-6">
                  <dt>GUEST</dt>
                  <dd v-for="(value, index) in guest" :key="index">{{value}}</dd>
                </div>
              </dl>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import main from './ListItem.js'
export default main
</script>
<style lang="scss" scoped>
@import './ListItem.scss'
</style>



