import _ from 'lodash'
import bus from '../../../bus.js'
import modal from './Modal.vue'
import upload from './Upload.vue'

export default {
  name: 'Tables',
  created () {
    bus.$on('getFirmwareList', async() => {
      this.getFirmwareList()
    })
  },
  components: { modal, upload },
  data () {
    return {
      active: 0,
      FirmwareList: {},
      popData: {},
      showModal: false
    }
  },
  mounted () {
    this.getFirmwareList()
  },
  methods: {
    delFireware (info) {
      bus.$emit('getFwInfo', info)
    },
    async getFirmwareList () {
      const list = await this.$api.getFirmwareList()
      list.data = _.each(list.data, (value, index) => {
        value.index = index
      })
      this.FirmwareList = _.orderBy(list.data, ['version'], ['desc'])
    },
    modalOpen: function (index) {
      this.showModal = true
      this.active = index
      return this.active
    }
  }
}



