<template>
  <div class="box box-primary">
    <div class="box-header with-border" v-if="title">
      <h3 class="box-title">{{ title }}</h3>
    </div>
    <form class="form-horizontal" @submit.prevent="submit">
      <div class="box-body">
        <div class="form-group" v-if="file" v-for="(item, id) in file" :key="id">
          <label :for="item.id" class="col-sm-3 control-label">
            {{ item.label }}
          </label>
          <div class="col-sm-1" v-show="!item.isLocal">
            <img height="34" @error="whenImageNotFound(id)" :src="getImageSrc(id)"/>
          </div>
          <div class="col-sm-8">
            <button
              @click.prevent="removeImage(id)"
              class="btn btn-default"
              type="button"
              v-show="!item.isLocal"
            >
              Remove Image
            </button>
            <input type="hidden" :name="'remove_' + id" :value="item.isLocal ? id : ''"/>
            <input
              v-if="item.isLocal"
              :class="item.class"
              :id="id"
              @change="onFileChange($event)"
              accept="image/*"
              class="none-image"
              type="file"
            >
          </div>
        </div>
        <div class="form-group" v-for="(item, id) in text" :key="id">
          <label :for="item.id" class="col-sm-3 control-label">
            {{ item.label }}
          </label>
          <div class="col-sm-9" v-if="isText(item.type)">
            <input
              :class="item.class"
              :id="id"
              :name="id"
              class="input-group input-group-lg"
              type="text"
              v-model="item.model"
              v-validate="item.validate"
            >
          </div>
          <div class="col-sm-9" v-if="isTextarea(item.type)">
            <textarea
              :class="item.class"
              :id="id"
              :name="id"
              rows="20"
              v-model="item.model"
              v-validate="item.validate"
            />
            <div class="feedback help is-danger" v-if="errors.has(id)">{{ errors.first(id) }}</div>
          </div>
        </div>
        <div class="form-group" v-if="alert$.show">
          <div class="col-sm-offset-2 col-sm-10">
            <div
              :class="alert$.class"
              class="alert"
              role="alert"
            >
              {{ alert$.text }}
            </div>
          </div>
        </div>
      </div>
      <div class="box-footer">
        <button
          v-for="(item, id) in buttons"
          :class="item.class"
          :disabled="loading || errors.any()"
          :key="id"
          :type="item.type"
          @click="item.onClick"
        >
          {{ item.text }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
  import main from './Theme.js'
  export default main
</script>

<style lang="scss" scoped>
  @import './Theme.scss'
</style>



