<template>
  <div class="modal fade" id="delete-form" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
          <h3 class="modal-title" id="exampleModalLabel">Are you sure you want to delete <small class="label label-default">{{info.version}}</small> ?</h3>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" @click="close">Close</button>
          <button type="submit" class="btn btn-primary" @click="deleteFw()">Delete</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import main from './Modal.js'
export default main
</script>

<style lang="sass">
@import './Modal.sass'
</style>



