import { quillEditor } from 'vue-quill-editor'

export default {
  components: {
    quillEditor
  },
  data () {
    return {
      name: 'base-example',
      content: `<h1>Privacy Policy</h1>
      Our privacy policy covers the collection and use of customer information when interacting with Exosite. Your privacy is important to us and we take every measure to ensure it is respected and protected. The protection of your privacy goes beyond visitation of our website, protection of your privacy is a company wide commitment amongst our team. Please read below for details regarding your privacy with interacting with our company and contact information for questions you may have.
      <h1>What Information We Collect and Why</h1>
      Collection of your information only comes through direct interaction with Exosite or its team members. This includes login to the Exosite website and/or interaction with Exosite team members around our offerings. We do not collect any information from you or on behalf of you without your knowledge. Exosite understands that your information, which you would knowingly provide to us, will not be sold, distributed or otherwise disseminated to any party outside of our organization other than partners or contractors if necessary. We take every precaution to protect your private information as if it were our own.
      Any information collected on you by Exosite will not be used, sold or distributed to any person or any organization outside of our own. There are times that it will be necessary or advantageous for Exosite to share your information with strategic vendors or third party contractors that are directly involved in our work with you. This usage will be specific in intent and limited to the current active engagement unless a request otherwise is made by you.
      Information Displayed in Public
      Any information that you display or contribute to public forums on our site is public information and not protected or preserved by Exosite
      Effective Date August 16th 2009
      <h1>Contact Information</h1>
      To ask for correction or removal of your information or to ask questions regarding this policy please contact:

      E-mail :: privacy@exosite.com

      Exosite, LLC
      275 Market Street, Suite 535
      Minneapolis, MN 55405 USA`,
      editorOption: {},
      editIcon: false,
      editMod: false,
      buttonOptions: [
        {
          class: 'btn btn-default',
          onClick: this.cancel,
          text: 'Cancel'
        },
        {
          class: 'btn btn-primary pull-right',
          onClick: this.cancel,
          text: 'Save'
        }
      ]
    }
  },
  methods: {
    cancel () {
      this.editMod = false
    },
    showEdit () {
      this.editIcon = !this.editIcon
    },
    edited () {
      this.editMod = true
    }
  },
  computed: {
    editor () {
      return this.$refs.myTextEditor.quill
    },
    showEditIcon () {
      return this.editIcon
    }
  }
}



