import DashView from './components/Dash.vue'
import LoginView from './components/Login.vue'
import NotFoundView from './components/404.vue'

// Import Views - Dash
import DeviceView from './components/views/device/Device.vue'
import FirmwareView from './components/views/firmware/Firmware.vue'
import ThemeView from './components/views/theme/Theme.vue'
import InformationModel from './components/views/informationModel/informationModel.vue'
import UserView from './components/views/user/User.vue'
import privacyPolicyView from './components/views/privacyPolicy/privacyPolicy.vue'

// Routes
const routes = [
  {
    path: '/login',
    component: LoginView
  },
  {
    path: '/',
    component: DashView,
    children: [
      {
        path: 'device',
        alias: '',
        component: DeviceView,
        name: 'Device List',
        meta: {description: ''}
      },
      {
        path: 'device/:sn',
        component: DeviceView,
        name: 'Device List',
        meta: {description: ''}
      },
      {
        path: 'firmware',
        component: FirmwareView,
        name: 'Firmware List',
        meta: {description: ''}
      },
      {
        path: 'theme',
        component: ThemeView,
        name: 'Theme',
        meta: {description: ''}
      },
      {
        path: 'theme/:id',
        component: ThemeView,
        name: 'Theme',
        meta: {description: ''}
      },
      {
        path: 'privacypolicy',
        component: privacyPolicyView,
        name: 'Privacy Policy',
        meta: {description: ''}
      },
      {
        path: 'informationModel',
        component: InformationModel,
        name: 'Information Model',
        meta: {description: ''}
      },
      {
        path: 'user',
        component: UserView,
        name: 'User List',
        meta: {description: ''}
      },
      {
        path: 'user/:email',
        component: UserView,
        name: 'User List',
        meta: {description: ''}
      }
    ]
  }, {
    // not found handler
    path: '*',
    component: NotFoundView
  }
]

export default routes



