import _ from 'lodash'
import bus from '../../bus'

export default {
  name: 'TimelineLog',
  computed: {
    isEmpty () {
      return Array.isArray(this.logList) && _.isEmpty(this.logList)
    }
  },
  data () {
    return {
      emptyMessage: 'No logs found.',
      hasLocation: {},
      loadingMessage: 'Loading...',
      status: {}
    }
  },
  methods: {
    async getLocation (index, status, ip) {
      if (_.isUndefined(this.hasLocation[index])) {
        this.hasLocation[index] = true
        let location = await window.jQuery
          .get(`https://freegeoip.net/json/${ip}`)
        status.content = `<div class="col-md-12 text-left"><strong>Location: ${location.region_name}, ${location.country_name}(${ip})</strong></div>${status.content}`
      }
    },
    clickId () {
      bus.$emit('TimelineModal-hide')
    },
    filter (type) {
      if (_.isUndefined(this.filterRule)) return true

      let targetType = _.toLower(type)
      switch (targetType) {
        case 'error':
          return this.filterRule[0].model
        case 'event':
          return this.filterRule[1].model
        default:
          break
      }
    },
    fromWhom (obj) {
      let { path } = this.$route
      if (path.includes('/device')) {
        return obj.action.email
      }
      if (path.includes('/user')) {
        return obj.action.sn
      }
      return ''
    },
    isError (type) {
      return _.toLower(type) === 'error'
    },
    navigate (obj) {
      let { path } = this.$route
      if (path.includes('/device')) {
        return `#user/${obj.action.email}`
      }
      if (path.includes('/user')) {
        return `#device/${obj.action.sn}`
      }
      return ''
    }
  },
  props: {
    connectivityStatus: {
      type: String
    },
    deviceStatus: {
      type: [Array, Object]
    },
    filterRule: {
      type: Array
    },
    logList: {
      required: true
    }
  }
}



